import { request } from "./request";
import Vue from 'vue'

export function myelecStationList() {
    return request({
        // url: '/web/apps/gongqi.hsb.elec/elecTwoAction/myelecStationList?id=' + sessionStorage.getItem('companyId') +
        // '&type=' + sessionStorage.getItem('companyType'),
        url:null,
        method: 'POST'
    })
}
export function overview(elecStationId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecTwoAction/overview?elecStationId=" + elecStationId,
        method: 'POST'
    })
}
