export function charts(chartsType, dayType, elecEqmId, elecStationId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecTwoAction/charts?chartsType=" + chartsType + '&dayType=' + dayType + '&elecStationId=' + elecStationId + '&elecEqmId=' + elecEqmId,
        method: 'POST',
        headers: { showLoading: false },
    })
}
