<!--选择站点组件-->
<template>
  <div class="stationSwitch">
    <el-input
      v-model="input"
      style="width: 55%"
      placeholder="模糊查询"
      @input="inputChange"
    ></el-input>
    <img class="closeImg" @click="hideSwitch" src="@/assets/powerStation/jin.png" alt="" />
    <div class="list">
      <div
        v-for="(item, index) in stationArry"
        :class="{ isActive: stationIndex == index }"
        :key="index"
        @click="changeStation(item, index)"
      >
        {{ item.stationName }}
<!--        {{item}}-->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    stationList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      stationIndex: 0,
      input: "",
      stationArry: [],
      fakeName:["高压电站","中心变"]
    };
  },
  watch: {
    stationList(val) {
      this.stationArry = val;
      this.stationListBase = val;
    },
  },
  mounted() {},
  methods: {
    /**
     * 隐藏
     * **/
    hideSwitch(){
      this.$bus.$emit('busHideSwitch',false)
    },
    /**
     * 切换电站
     * **/
    changeStation(item, index) {
      this.stationIndex = index;
      // 向父组件传值
      this.$bus.$emit("busElecStationId", item.stationId);
    },
    inputChange(content) {
      let arry = [];
      this.stationListBase.forEach((item) => {
        if (item.stationName.includes(content)) {
          arry.push(item);
        }
      });
      this.stationArry = arry;
    },
  },
};
</script>
<style scoped lang="less">
.list {
  border-top: 1px solid rgba(222, 222, 222, 0.75);
  > div {
    position: relative;
  }
}
/deep/ .el-input__inner {
  height: 35px;
  line-height: 35px;
}
.stationSwitch {
  position: absolute;
  top: 0.5rem;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  height: calc(100% - 0.5rem);
  width: 5rem;
  z-index: 99;
  border-radius: 5px 0 0 5px;
  > div {
    display: flex;
    flex-wrap: wrap;
    padding: 0.1rem;
    // border-top: 1px solid rgba(222, 222, 222, 0.75);
    > div {
      margin: 5px;
      font-size: 0.18rem;
      padding: 0.1rem 0.25rem;
      cursor: pointer;
      background: #fff;
      color: #0072ff;
      border-radius: 0.05rem;
      font-weight: bold;
    }
  }
}
.isActive::before {
  content: "";
  position: absolute;
  right: -1px;
  bottom: -0.5px;
  width: 0.25rem;
  height: 0.25rem;
  background: url("@/assets/icon-tag-swiched.png");
  background-size: cover;
  z-index: 1;
  display: block;
  transition: all 0.5s;
  background-size: 100% 100%;
}
.closeImg {
  position: absolute;
  right: 10px;
  width: 25px;
  top: 15px;
  cursor: pointer;
}
</style>